@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@400;600&display=swap);

*{font-family:"Titillium Web", sans-serif;font-size:16px;font-weight:400}h1{font-size:28px;line-height:36px}@media screen and (min-width: 768px){h1{font-size:32px;line-height:40px}}h2{font-size:20px;line-height:28px}@media screen and (min-width: 768px){h2{font-size:24px;line-height:32px}}h3{font-size:13px;line-height:20px;font-weight:600}@media screen and (min-width: 768px){h3{font-size:14px;line-height:22px}}h4{font-size:11px;line-height:16px}@media screen and (min-width: 768px){h4{font-size:12px;line-height:18px}}

*{box-sizing:border-box;margin:0}.main{background-color:#f7f8f9;height:100vh;color:#13182c}.main__movies{background-color:#f7f8f9;padding-right:1.5rem;padding-left:1.5rem;display:flex;justify-content:space-between}.main__movie-heading{font-weight:bold;margin-bottom:2rem}.main__movie-results{width:50%}.main__movie-nominated{width:50%}

.head{display:flex;box-shadow:2px 4px 1px 1px rgba(0,0,0,0.5);justify-content:space-between;background-color:#232940}.head__logo{height:7rem;width:11rem}@media screen and (min-width: 768px){.head__logo{height:9rem;width:14rem}}@media screen and (min-width: 1200px){.head__logo{height:11rem;width:18rem}}.head__text-container{display:flex;justify-content:space-evenly;align-items:center;width:50%}@media screen and (min-width: 768px){.head__text{font-size:25px}}.head__text-link,.head :visited,.head :link{text-decoration:none;color:#fff;font-size:25px}.head__text-link:hover,.head :visited:hover,.head :link:hover{font-weight:bold}.is--active{border-bottom:3px solid #DCA84D}

.search{padding-right:1.5rem;padding-left:1.5rem;background-color:#f7f8f9;color:#13182c}.search__head-container{padding-top:2rem}.search__head{font-weight:bold;margin-bottom:1rem}.search__subhead{font-weight:bold;margin-bottom:1rem}.search__input-form{display:flex;flex-direction:column}.search__input-field{border:0.0625rem solid #bdc5d5;background-color:#fff;border-radius:1.25rem;height:2.25rem;width:100%;margin-bottom:1.5rem;padding-left:0.625rem}.search__input-field:focus{outline:none;outline-color:#232940;border:1.5 px solid #232940}@media screen and (min-width: 768px){.search__input-field{width:45%;padding-left:1.625rem}}.search__input-btn{width:100%;height:2.25rem;margin-bottom:1rem;background-color:#2e66e6;border-radius:1.25rem;color:#fff;border:none;cursor:pointer}.search__input-btn:hover{background-color:#232940}.search__input-btn:focus{outline:none;border:1.5 px solid #232940}@media screen and (min-width: 768px){.search__input-btn{width:20%}}

.movie{display:flex;flex-direction:column}.movie__container{margin-bottom:1rem;margin-top:1rem}.movie__poster{height:10rem;width:8rem;margin-bottom:1rem;transition:transform 0.5s}.movie__poster:hover{transform:scale(1.2)}.movie__blurb-text{font-weight:bold;margin-bottom:1rem}.movie__btn{width:6.625rem;height:2.25rem;margin-bottom:1rem;background-color:#2e66e6;border-radius:1.25rem;color:#fff;border:none;cursor:pointer}.movie__btn:hover{background-color:#232940}.movie__btn:focus{outline:none;border:1.5 px solid #232940}.movie__error-container{display:flex;flex-direction:column}.movie__error-img{width:5rem;margin-bottom:1rem}@media screen and (min-width: 768px){.movie__error-img{width:10rem}}

.nominated__container{margin-bottom:1rem;margin-top:1rem}.nominated__poster{height:10rem;width:8rem;margin-bottom:1rem;margin-top:0.6875rem;transition:transform 0.4s}.nominated__poster:hover{transform:scale(1.2)}.nominated__blurb-text{font-weight:bold;margin-bottom:1rem}.nominated__btn{width:6.625rem;height:2.25rem;margin-bottom:1rem;background-color:#2e66e6;border-radius:1.25rem;color:#fff;border:none;cursor:pointer}.nominated__btn:hover{background-color:#232940}.nominated__btn:focus{outline:none;border:1.5 px solid #232940}

.story{padding-right:1.5rem;padding-left:1.5rem;background-color:#f7f8f9;color:#13182c;padding-bottom:3rem;height:100vh}.story__heading{padding-top:4rem;margin-bottom:1rem;font-weight:bold}.story__blurb{font-size:1.5rem;line-height:2rem;font-weight:lighter}

